	import ePicker from '@/components/e-picker/e-picker.vue';
	import myPickerPart from "@/components/myPickerPartProps.vue";
	import uniIcon from "@/components/uni-icons/uni-icons.vue";
	let _this;
	
	export default {
		components:{
			myPickerPart,
			uniIcon,
			ePicker
		},
		data() {
			return {
				delivery: ['现货','定织'],
				activeIndex: -1,
				isTaxSelect: false,
				isCarriageSelect: false,
				
				
				tradeName:'',      //关联的品名
				companyName:'',    //公司名
				price1:'',
				price2:'',
				form:{
					    companyCode:'',				//联系人编码
						buyOrSellCode:'',			//联系人编码
						tradeName:'',				//品名(别名)
						price:'',                   //目标价
                        isPlusDuty:'',	            //是否含税
                        isIcash:''			        //是否含运费

				    }
				
			};
		},
		onLoad:function(){
			_this = this
		},
		methods:{
			
			
			
			
			selectButton:function(e){
				console.log(e)
				this.activeIndex = e
			},
			
			
			toSelectInquiry:function(){
				uni.navigateTo({
					url: './connectInquiry',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
		    tapTaxButton:function(){
				this.isTaxSelect = !this.isTaxSelect
			},
			tapCarrButton:function(){
				this.isCarriageSelect = !this.isCarriageSelect
			},
			areaInput:function(e){
				console.log(e)
			},
			selectBuyer:function(){
				uni.navigateTo({
					url: '/pages/qing-f-c/newFollowRecord/selectBuyer',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			},
			async submit(){
				
                if (this.price2 < this.price1){
					uni.showToast({
						title: '价格范围有误',
						icon: 'none'
					});
					return
				}
				this.form.price = this.price1 +'~'+this.price2
				this.form.isPlusDuty = this.isTaxSelect? 1 : 0
				this.form.isIcash = this.isCarriageSelect? 1 : 0
				let data=this.form
				
                console.log(data)
				uni.showLoading({
					title: '提交中',
					mask: true
				})
				const res = await this.$http.post('/sQuotation/rapid_add',{data:data})
				console.log(res)
				uni.hideLoading()
				var pages = getCurrentPages();
				var currPage = pages[pages.length - 1]; //当前页面
				var prevPage = pages[pages.length - 2]; //上一个页面
				//直接调用上一个页面的setData()方法，把数据存到上一个页面中去
				prevPage.setData({
				   isDoRefresh:true
				})
				
				uni.redirectTo({
					url: `./casualList`
				})
				
			}
		},
		
	}
